// popup ::::::::::::::::::=>
#exit {
  position: relative;
  padding: 5px;
  margin: 1em;
  float: right;
  z-index: 9999;
  // width: 30px;
  i {
    color: $green;
    font-size: 2.1em;

  }
}

#exit1, #exit2 {
  @extend #exit;
}

.popup {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1111;

  .signup {
    position: relative;
    top: 4%;
  }

}

.sign-in {
  top: 15%;
  position: relative;
  @media (max-width: 768px) {
    top: 5%;
  }
}


.inscription-form2 {

  background-color: white;
  position: absolute;
  z-index: 1111;
  top: 4%;
  left: 35%;
  right: 35%;
  // bottom: 80px;
  //margin-left: 40%;
  padding: 5em;
  border-radius: 30px;

  .containre-form2 {

    h1 {
      //margin-top: -2em;
      margin-bottom: 2em;
      text-align: center;
    }

    .inswerinput {
      //width: max-content;
      // border-radius: 20px;
      // width: 900px;
      //margin-bottom: .5em;
      // padding: 8px 90px;
      // position: relative;
      // width: 5px;

      input[type="radio"] {
        -webkit-appearance: none;
      }

      input[type="radio"]:checked ~ .inswerinput {
        background-color: black;
      }

      input[type="radio"]:checked ~ label {
        background-color: black;
      }

      .bgclor2 {
        background-color: #F9A826;
      }

      .bgclor3 {
        background-color: #00BFA6;
      }

      label {
        color: white;
        padding: .5em 1em;
        margin-bottom: .5em;
        border-radius: 2em;
        background-color: #F50057;
        width: 20vw;
        height: 3em;
        text-align: center;

        // span {
        //   // padding: 8px 15px;
        //   //font-size: 21px;

        // }


      }

      // .widthlabel{
      //     width: 200px;
      // }

    }

    button {
      @include btn(transparent, 10em);
      height: 3em;
      color: black;
      margin: 1.2em auto;

    }

    .bgclor2 {
      background-color: #F9A826;
    }

    .bgclor3 {
      background-color: #00BFA6;
    }
  }

}


//*****************
.spec {
  position: relative;
  z-index: 99999;

  .inscription-form3 {

    background-color: white;
    position: absolute;
    z-index: 1111;
    top: 10px;
    left: 30%;
    right: 30%;
    padding: 80px 80px;
    border-radius: 30px;

    h1 {
      text-align: center;
      margin-bottom: 1em;
    }

    .inswerinput3 {

      margin-bottom: .5em;
      border-radius: 2em;
      background-color: #F50057;
      text-align: center;
      width: max-content;
      //background-color: #F50057;
      //width: 300px;

      input[type="radio"] {
        -webkit-appearance: none;

      }

      input[type="radio"]:checked ~ label {
        background-color: #F4CF1B;

      }

      label {
        color: white;
        padding: .5em 1em;
        width: 25vw;
        margin-bottom: 1em;
        border-radius: 1.5em;
        height: 6em;


        span {
          font-size: 22px;
        }
      }

    }

    button {
      @include btn(transparent, 10em);
      height: 3em;
      color: black;
      margin: 1.2em auto;

    }

    .bgclor2 {
      background-color: #F9A826;
    }

    .bgclor3 {
      background-color: #00BFA6;
    }
  }
}


@media (max-width: 763px) {
  * {
    font-size: 10px;
  }
  .inscription-form3 {
    left: 20%;
    right: 20%;

    label {
      width: 30vw;

    }
  }
  .inscription-form2 {
    left: 26%;
    right: 26%;

    label {
      width: 20vw;

    }
