.signup {
  margin-bottom: 150px;
}

.signup-content {
  @extend display-flex;
  padding: 30px 0;
  // flex-flow: wrap;
}

.signup-form, .signup-image, .signin-form, .signin-image {
  width: 50%;
  overflow: hidden;
}

.signup-image {
  margin: 0 55px;
}

.form-title {
  margin-bottom: 33px;
}

.signup-image {
  margin-top: 45px;
}

figure {
  margin-bottom: 50px;
  text-align: center;
}

.form-submit {
  @include btn($orange, auto);
  border-radius: 5em;
  display: inline-block;
  //background: $blue-color;
  color: #fff;
  //border-bottom: none;
  padding: 15px 39px;
  @include border-radius(5px);
  margin-top: 25px;
  cursor: pointer;

  &:hover {
    background: darken($orange, 10%);
  }
}

#signin {
  margin-top: 16px;
}

.signup-image-link {
  font-size: 14px;
  color: $black-color;
  display: block;
  text-align: center;
}

.term-service {
  font-size: 13px;
  color: $black-color;
}

.signup-form {
  margin-left: 75px;
  margin-right: 75px;
  padding-left: 34px;
}

.register-form {
  width: 100%;
}

.form-group {
  position: relative;
  margin-bottom: 25px;
  overflow: hidden;

  &:last-child {
    margin-bottom: 0px;
  }
}

input {
  width: 100%;
  display: block;
  border: none;
  border-bottom: 1px solid $grey-light;
  padding: 6px 30px;
  font-family: Poppins;
  box-sizing: border-box;
  @include input-placeholder($grey-light);

  &:focus {
    border-bottom: 1px solid $black-color;
    @include input-placeholder($black-color);
  }
}

input[type=checkbox]:not(old) {
  width: 2em;
  margin: 0;
  padding: 0;
  font-size: 1em;
  display: none;
}

input[type=checkbox]:not(old) + label {
  display: inline-block;
  line-height: 1.5em;
  margin-top: 6px;
}

input[type=checkbox]:not(old) + label > span {
  display: inline-block;
  width: 13px;
  height: 13px;
  margin-right: 15px;
  margin-bottom: 3px;
  border: 1px solid $grey-light;
  @include border-radius(2px);
  background: rgb(255, 255, 255);
  background-image: -moz-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: -ms-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: -o-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: -webkit-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  vertical-align: bottom;
}

input[type=checkbox]:not(old):checked + label > span, {
  background-image: -moz-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: -ms-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: -o-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: -webkit-linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
  background-image: linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
}

input[type=checkbox]:not(old):checked + label > span:before {
  content: '\f26b';
  display: block;
  color: $black-color;
  font-size: 11px;
  line-height: 1.2;
  text-align: center;
  font-family: 'Material-Design-Iconic-Font';
  font-weight: bold;
}

.agree-term {
  display: inline-block;
  width: auto;
}

