@media screen and (max-width: 768px) {

  .signup-content, .signin-content {
    @include flex-direction(column);
    @include justify-content(center);
    padding-top: 25px;
    padding-bottom: 25px;
  }
  .signup-form {
    margin-left: 0px;
    margin-right: 0px;
    padding-left: 0px;
    /* box-sizing: border-box; */
    padding: 0 30px;
  }
  .signin-image {
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 0px;
    @include order(2);
    display: none;

  }
  .signup-image {
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 10px;
    width: 60%;
    @include order(2);

    display: none;
  }
  .signup-form, .signup-image, .signin-form, .signin-image {
    width: auto;
  }

  .social-login {
    @include justify-content(center);
  }
  .form-button {
    text-align: center;
  }
  .signin-form {
    @include order(1);
    margin-right: 0px;
    margin-left: 0px;
    padding: 0 30px;
  }
  .form-title {
    text-align: center;
  }

}

